import React, { useRef, useState } from "react";
import emailjs from "@emailjs/browser";
import "../styles/Contact.css";
import "../styles/Services.css";
import validator from 'validator';

const ContactEmail = () => {
  const form = useRef();
  const [user_name, setUsername] = useState('');
  const [nameError, setNameError] = useState('')
  const [user_email, setUseremail] = useState('');
  const [emailError, setEmailError] = useState('');
  const [user_phone, setUserphone] = useState('');
  const [phoneError, setPhoneError] = useState('');
  const [user_address, setUseraddress] = useState('');
  const [AddressError, setAddressError] = useState('')
  const [message, setUsermessage] = useState('');
  const [messageError, setMessageError] = useState('')

  const handleChange = e => {
    setUsermessage(e.target.value);
    setUseraddress(e.target.value);
    setUsername(e.target.value);
    setUseremail(e.target.value);
    setUserphone(e.target.value);
  };

  const validateEmail = (e) => {
    var user_email = e.target.value

    if (validator.isEmail(user_email)) {
      setEmailError('Valid Email')
    } else {
      setEmailError('Enter valid Email!')

    }
  }

  const sendEmail = (e) => {
    e.preventDefault();

    setUsermessage(e.target.value);
    setUseraddress(e.target.value);
    setUserphone(e.target.value);
    setUsername(e.target.value);


    if (user_name.length == 0) {
      setUsername('Name cannot be empty');
    }

    if (user_address.length == 0) {
      alert('Address cannot be  empty');
    }

    if (message.length == 0) {
      alert('Message cannot be  empty');
    }

    if (user_phone.length == 0) {
      alert('Phone number cannot be  empty');
    }
    if (user_email.length == 0) {
      alert('Email cannot be  empty');
    }

    emailjs
      .sendForm(
        "service_y7jlmtp",
        "template_ni7y9j7",
        form.current,
        "S1nmzCxbTJTq8FMi8"
      )
      .then(
        (result) => {
          console.log(result.text);
          console.log("message sent");
        },
        function (error) {
          console.log(error.text);
        }
      );
  };

  return (

    <form className="Contactform" ref={form} onSubmit={sendEmail}>
      <div className="row">
        <div className='OpeningHours1'> </div>

        <div className='col-xxl-2 col-xl-2 col-lg-2 col-md-2 col-sm-2 EmailContent openingtitle'>

          <input className="InputBox" onChange={handleChange} type="text" name="user_name" placeholder="Name" />

        </div>

        <div className='col-xxl-2 col-xl-2 col-lg-2 col-md-2 col-sm-2 EmailContent openingtitle'>
          <input className="InputBox" onChange={validateEmail} type="email" name="user_email" placeholder="Email" />
          <br />
          {emailError}
        </div>

        <div className='OpeningHours1'></div>
        <div className='col-xxl-2 col-xl-2 col-lg-2 col-md-2 col-sm-2 EmailContent openingtitle'>
          <input className="InputBox" onChange={handleChange} type="number" name="user_phone" placeholder="Phone" />
        </div>


        <div className='col-xxl-2 col-xl-2 col-lg-2 col-md-2 col-sm-2 EmailContent openingtitle'>
          <input className="InputBox" onChange={handleChange} type="text" name="user_address" placeholder="Address" />
        </div>

        <div className='OpeningHours1'></div>

        <div className='col-xxl-4 col-xl-4 col-lg-4 col-md-4 col-sm-4 EmailContent openingtitle'>
          <input className="InputBox1" onChange={handleChange} name="message" placeholder="Type your message here..." />
        </div>

        <div className='OpeningHours1'></div>
        <div className='col-xxl-2 col-xl-2 col-lg-2 col-md-2 col-sm-4 openingtitle'>
          <input className="InputBox2" type="submit" value="Submit" onSubmit={sendEmail} />
        </div>
      </div>
    </form>
  );
};

export default ContactEmail;